body {
    margin: 0;
    padding: 0;
    background-color: #cbe3ff;
    font-family: "Poppins", sans-serif;
    /* position: relative; */
    width: 100vw;
    height: 100vh;
    /* overflow-x: hidden; */
    box-sizing: border-box;
  }
  .nav-main{
    display: flex;
    align-items: center;
  }
  
  .navbar {
    background-color: rgb(255, 255, 255);
    position: fixed;
    z-index: 100;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0px 30px;  
    box-sizing: border-box;
  }
  .dropbtn {
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
    border: none;
    cursor: pointer;
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  /* .dropbtn i {
    margin: 0em 0.5em;
  } */
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
  }
  
  .dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }
  
  .dropdown-content a:hover {
    background-color: #ddd;
  }
  .logo {
    color: rgb(0, 0, 0);
  }
  .dropdown:hover .dropdown-content {
    display: block;
  }
  .dropbtn p{
    margin: 0em;
    padding: 0em 1em;
  }
  .content-cp {
    width: 100%;
    /* border: 2px solid red; */
    top: 160px;
    left: 0px;
    right: 0px;
    /* padding: 95px 30px; */
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .container-cp  {
    min-width: 30%;
    height: auto;
    /* border: 2px solid red; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    box-shadow: 0.5px 0.5px 20px 8px rgba(28, 28, 28, 0.532);
    background-color: #cbe3ff;
    padding: 2em 2em;
    margin: 0em 2em;
    /* text-align: center; */
  }
  .form{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  input[type="text"],input[type="password"]{
    /* min-width: 80%; */
    height: 40px;
    padding: 0em 0.8em;
    background-color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 0.5px 0.5px 10px 1px rgba(28, 28, 28, 0.167);
    outline: none;
  }
  #currentpasswordmgs,#newpasswordmgs,#confirmpasswordmgs{
    margin-bottom: 1em;
  }
  #btn{
    height: 40px;
    background-color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 0.5px 0.5px 10px 1px rgba(28, 28, 28, 0.167);
    background-color: #0c0765;
    color: white;
    margin-bottom: 1em;
  }
  #currentpasswordmgs,
  #newpasswordmgs,
  #confirmpasswordmgs{
  color: red;
  display: flex;
  font-size: small;
  margin-left: 0.2em;
  }
  
  #newpasswordmgs li{
  margin-left: 1em;
  }
  ul{
  margin: 0em;
  padding: 0em;
  }
  ul p{
  margin: 0px;
  }
  h2{
    text-align: center;
  }