.center {
  background: rgb(28, 31, 110);
  background: linear-gradient(
    90deg,
    rgba(28, 31, 110, 1) 50%,
    rgba(203, 227, 255, 1) 50%
  );
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container-forgot {
  width: 40vw;
  height: 45vh;
  /* border: 2px solid red; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  box-shadow: 0.5px 0.5px 20px 8px rgba(28, 28, 28, 0.532);
  background-color: #cbe3ff;
  margin: 0em 1em;
}
h1 {
  font-size: medium;
  font-weight: 600;
  margin: 1em;
  text-align: center;
}
#email {
  width: 100%;
  /* height: 3em; */
  background-color: #ffffff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  /* box-shadow: 0.5px 0.5px 10px 1px rgba(28, 28, 28, 0.107); */
  box-shadow: 0.5px 0.5px 10px 1px rgba(28, 28, 28, 0.26);
  margin-bottom: 1em;
  padding: 1em;
  outline: none;
  /* margin: 1em; */
}
.form {
  width: 100%;
  padding: 1em 2em;
}
#emailvalid {
  color: red;
  display: flex;
  font-size: small;
  margin-left: 1em;
}
.btn {
  width: 100%;
  height: 3em;
  background-color: #0c0765;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  box-shadow: 0.5px 0.5px 10px 1px rgba(28, 28, 28, 0.26);
  margin: 1em 0em;
  color: white;
  font-weight: 500;
}
h3 {
  font-size: smaller;
  font-weight: 400;
  margin: 1em;
  text-align: center;
}
@media screen and (max-width: 768px) {
  .container {
    width: auto;
    height: 45%;
  }
}
