
h1 {
  font-size: medium;
  font-weight: 600;
  text-align: center;
  color: #021832;
  text-transform: uppercase;
  border-bottom: 2px solid #021832;
  margin: 0px 20px;
}
body{
  width: 100vw;
  height: 100vh;
}
.center {
  background: rgb(28, 31, 110);
  background: linear-gradient(
    90deg,
    rgba(28, 31, 110, 1) 50%,
    rgba(203, 227, 255, 1) 50%
  );
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container-login {
  width: 70vw;
  height: 70vh;
  /* border: 2px solid red; */
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  box-shadow: 0.5px 0.5px 20px 8px rgba(28, 28, 28, 0.532);
}
.main-form {
  width: 50%;
  height: 100%;
  /* border: 2px solid green; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #cbe3ff;
  border-radius: 8px 0px 0px 8px;
  text-align: center;
  padding: 1em 3em;
}
.form {
  width: 100%;
}
.main-img {
  width: 50%;
  height: 100%;
  border-radius: 0px 8px 8px 0px;
  background-color: white;
  /* border: 2px solid green; */
}
#emailvalid {
  /* border: 2px solid red; */
  color: red;
  display: flex;
  font-size: small;
  margin-top: 0.5em;
}
#passvalid {
  color: red;
  display: flex;
  font-size: small;
  margin-top: 0.5em;
}
.main-img img {
  border-radius: 0px 8px 8px 0px;
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.normal_text {
  width: 100%;
  height: 35px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  column-gap: 10px;
}
.normal_text {
  font-size: small;
  font-weight: 300;
}
.normal_text::before,
.normal_text::after {
  content: "";
  height: 0.5px;
  background-color: rgba(0, 0, 0, 0.692);
  display: block;
}
.main-form h1,
h2 {
  font-size: x-large;
  font-weight: 700;
  /* margin: 0.5em; */
}
.main-form h2 {
  margin-bottom: 0.5em;
}
.btn-loginPage {
  width: 90%;
  height: 35px;
  background-color: #0c0765;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  box-shadow: 0.5px 0.5px 10px 1px rgba(28, 28, 28, 0.26);
  margin-bottom: 1em;
}
.btn i {
  margin-right: 1em;
}
input[type="text"],
input[type="password"] {
  width: 100%;
  height: 35px;
  background-color: white;
  border: none;
  border-radius: 8px;
  padding-left: 1em;
  /* margin: 0.2em 1em; */
  outline: none;
  box-shadow: 0.5px 0.5px 10px 1px rgba(28, 28, 28, 0.26);
}
.keepinfo-data {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  /* border: 2px solid rgb(10, 9, 9); */
  width: 50%;
  height: 100%;
}
.keepmedata {
  margin-left: 1px;
}
.keepinfo-data h3 {
  font-size: x-small;
  font-weight: 400;
}
.other-info {
  display: flex;
  justify-content: space-around;
  align-items: center;
  /* border: 2px solid red; */
  width: 100%;
  height: 35px;
}
.forgot {
  /* border: 2px solid green; */
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: end;
  align-items: center;
}
.forgot a {
  font-size: x-small;
  /* text-decoration: none; */
}
#submit-btn {
  width: 100%;
  height: 35px;
  background-color: #0c0765;
  border: none;
  color: white;
  border-radius: 8px;
  margin: 0.3em 0em;
  cursor: pointer;
}
.noaccount {
  width: 100%;
  margin-top: 1em;
  font-size: small;
  font-family: 300;
}

@media screen and (max-width: 768px) {
  .main-img {
    display: none;
  }
  .container-login {
    width: 90vw;
    height: auto;
  }
  .main-form {
    width: 100%;
    border-radius: 8px;
  }
  .normal_text {
    font-size: xx-small;
    font-weight: 300;
    column-gap: 4px;
  }
  .normal_text::before,
  .normal_text::after {
    content: "";
    height: 0.5px;
    background-color: rgba(0, 0, 0, 0.692);
    display: block;
  }
  .main-form h1,
  h2 {
    font-size: large;
    font-weight: 600;
    margin: 0.5em 0em;
  }
  .main-form h2 {
    margin-bottom: 0.5em;
  }
