  .container-signup {
    width: 30vw;
    height: auto;
    /* border: 2px solid red; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    box-shadow: 0.5px 0.5px 20px 8px rgba(28, 28, 28, 0.532);
    background-color: #cbe3ff;
    padding: 1em 3em;
    margin: 0em 2em;
    /* text-align: center; */
  }
  .form{
    width: 100%;
  }
  h1 {
    font-size: medium;
    font-weight: 600;
    margin: 1em;
  }
  h3 {
    font-size: smaller;
    font-weight: 400;
    margin: 1em 0em;
  }
  .btn {
    width: 100%;
    height: 3em;
    background-color: #0c0765;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 0.5px 0.5px 10px 1px rgba(28, 28, 28, 0.167);
    margin: 0.2em;
    color: white;
    font-weight: 500;
  }
  input[type="text"],
  input[type="password"] {
    width: 100%;
    height: 3em;
    background-color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 0.5px 0.5px 10px 1px rgba(28, 28, 28, 0.167);
    margin-bottom: 1em;
    padding: 1em;
    outline: none;
    margin: 0.2em;
  }
  #passwordmgs,
  #emailmgs,
  #lastnamemgs,
  #firstnamemgs {
    color: red;
    display: flex;
    font-size: small;
    margin-left: 0.2em;
  }
  #passwordmgs li{
    margin-left: 1em;
  }
  